import * as d3 from 'd3';

interface MyNode extends d3.SimulationNodeDatum {
  id: string;
}

interface MyLink extends d3.SimulationLinkDatum<MyNode> {
}

self.onmessage = (event: MessageEvent<{ nodes: MyNode[], links: MyLink[], width: number, height: number }>) => {
  const { nodes, links, width, height } = event.data;
  // @ts-ignore: Suppress 'unused variable' warning
  const simulation = d3.forceSimulation<MyNode>(nodes)
    .force('link', d3.forceLink<MyNode, MyLink>(links).id(d => d.id))
    .force('charge', d3.forceManyBody().strength(-300))
    .force('center', d3.forceCenter(width / 2, height / 2))
    .on('tick', () => {
      self.postMessage({ nodes });
    });
};